import React from "react";

export default class ErrorBoundaries extends React.Component {
  constructor(props) {
    super(props);
    this.state = { error: null, errMsg: null };
  }
  static getDerivedStateFromError(error) {
    return { error: error, errMsg: error.message };
  }
  // }
  // componentDidCatch(error, errMsg) {
  //   console.log(arguments);
  //   this.setState({ error: error.toString(), errMsg: errMsg.componentStack });
  // }
  render() {
    if (this.state.error) {
      return <div>Error roi ne. Cu the nhu sau: {this.state.errMsg}</div>;
    }
    return this.props.children;
  }
}
