import React from "react";
import { Outlet, Link } from "react-router-dom";
export default class Layout extends React.Component {
  render() {
    return (
      <div>
        <h1>Layout</h1>
        <nav>
          <Link to="/about">About</Link> | <Link to="/home">Home</Link>
        </nav>
        <Outlet />
      </div>
    );
  }
}
